import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { AuthProvider } from '../../providers/auth/auth';
import { User, Collections } from '../../app/app.models';
import { AngularFirestore } from '@angular/fire/firestore';
import { take } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  public uid: string = this.navParams.get('uid')
  public edit: boolean = false
  public user: User

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public back: BackProvider,
    private toast: ToastController,
    private load: LoadingController,
    public auth: AuthProvider,
    private afs: AngularFirestore
    ) {
  }

  ionViewDidLoad() {

    if (!this.auth.user) return this.navCtrl.setRoot('LoginPage')

    if (!this.uid) {
      this.user = this.auth.user
      this.auth.user$.subscribe(_user => {
        if (_user && !this.user) {
          this.afs.collection(Collections.USER).doc<User>(_user.uid).valueChanges().subscribe(user => this.user = user)
        }
      })
    } else {
      this.afs.collection(Collections.USER).doc<User>(this.uid).valueChanges().subscribe(user => this.user = user)
    }
  }

  async loadUser(uid: string) {
    const user = await this.afs.collection(Collections.USER).doc<User>(uid).valueChanges().pipe(take(1)).toPromise()
    this.user = user
  }

  onFinish(data: any) {
    this.user.credencial = data.secure_url
  }

  async saveChanges() {

    const l = this.load.create({content: 'Guardando cambios...'})
    l.present()

    await this.afs.collection(Collections.USER).doc(this.user.uid).update(this.user)

  }

  openImage(url: string) {
    if (!this.auth.isAdmin) return
    console.log(url);
    
    let w = window.open(url, '_blank')
    w.focus()
  }

}



